/* eslint-disable */

// import .
import React from "react";
import styled from "styled-components";
import { Grid } from "../../../elements/Index";
import BookmarkIcon from "@mui/icons-material/Bookmark";
import BookmarkBorderIcon from "@mui/icons-material/BookmarkBorder";

// Bookmark   .
const Bookmark = (props) => {
  return (
    <React.Fragment>
      <BookMarkMedia>
        {props.userId !== props.postUserId && (
          <BookMark>
            <BookMark onClick={props.ToggleBookMark}>
              {!props.passedData?.bookmarkChecked ? (
                <BookmarkBorderIcon sx={{ color: "#172D40", fontSize: 45 }} />
              ) : (
                <BookmarkIcon sx={{ color: "#172D40", fontSize: 45 }} />
              )}
            </BookMark>
          </BookMark>
        )}
      </BookMarkMedia>
    </React.Fragment>
  );
};

const BookMarkMedia = styled.div`
  @media screen and (max-width: 600px) {
    width: 10px;
    height: 10px;
    font-size: 10px;
  }
`;
const BookMark = styled.div`
  width: 50px;
  position: absolute;
  top: -10px;
  right: -30px;
  height: 50px;
  cursor: pointer;
  @media screen and (max-width: 600px) {
    width: 30px;
    height: 30px;
    right: 15px;
  }
`;

// export      .
export default Bookmark;
